import React, { Component } from 'react';
import styled from 'styled-components';
import posed from 'react-pose'

const SearchWrapper = posed.div({
  expanded: {
    width: '100vw',
    height: '100vh',
    y: -36,
  },
  collapsed: {
    width: 'calc(100vw - 32px)',
    height: '40px',
    y: -20,
  }
})

const StyledSearchWrapper = styled(SearchWrapper)`
  width: calc(100vw - 32px);
  height: 40px;
  background: #FFFFFF;
  box-shadow: 0 2px 16px -2px rgba(0,0,0,0.32);
  border-radius: ${props => props.expanded ? '0' : '4px'};
  margin: auto;
  position: ${props => props.expanded ? 'fixed' : 'sticky'};
  transform: translateY(-20px);
  top: 36px;
  overflow: hidden;
  box-sizing: border-box;
  padding: ${props => props.expanded ? '16px' : 0};
  z-index: 8;
`;

const SearchInputWrapper = styled.div`
  width: 100%;
  height: 100%;
  position: relative;
  box-sizing: border-box;
  padding: 0 0 0 40px;
`;

const SearchInput = styled.input`
  width: 100%;
  height: 100%;
  max-height: 40px;
  border-radius: 0;
  border: 0;
  box-shadow: none;
  appearance: none;
  position: relative;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding: 0 8px;
  margin: 0;
  font-size: 1rem;
  font-weight: 400;
  color: #0f0f0f;
  transition: 0.4s ease-out all;

  &::placeholder {
    font-size: 1rem;
    font-weight: 400;
    color: #9f9f9f;
  }

  &:focus {
    outline: none;
  }
`;

const SearchIcon = styled.i`
  width: 40px;
  height: 40px;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  color: ${props => props.expanded ? '#0f0f0f' : '#9f9f9f'};
  transition: 0.4s ease-out all;
  cursor: ${props => props.expanded ? 'pointer' : 'default'};
`;

class Search extends Component {

  constructor(props) {
    super(props);
    this.state = {
      expanded: false,
    }
  }

  render() {


    return (
      <StyledSearchWrapper pose={this.state.expanded ? 'expanded' : 'collapsed'} expanded={this.state.expanded} className="sticky-events">
        <SearchInputWrapper>
          <SearchIcon className="material-icons" onClick={() => this.state.expanded === true && this.setState({ expanded: false })} expanded={this.state.expanded}>
            {this.state.expanded ? 'close' : 'search'}
          </SearchIcon>
          <SearchInput
            type="search"
            onFocus={() => this.state.expanded === false && this.setState({ expanded: true })}
            placeholder="Search Dishes"/>
        </SearchInputWrapper>
      </StyledSearchWrapper>
    );
  }

}

export default Search;
